.App {
  text-align: center;
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
}

.App-logo {
  height: 40px;
  pointer-events: none;
  float:left;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navigation {
  padding-top: 1.5vw;
  padding-bottom: 1.5vw;
  padding-left: 2vw;
  padding-right: 2vw;
  overflow: hidden;
}

.site-title {
  float:left;
}

.site-title .item {
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 700;
  line-height: 1.4em;
  font-size: calc(0.5vw + 1rem);
  color: #333;
  padding: .1em 0;
  text-decoration: none;
}

.sub-nav {
  float: right;
}

.sub-nav a {
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.8em;
  font-size: 1rem;
  color: #000;
  padding: .1em 0;
  opacity: 0.8;
  text-decoration: none;
}

.sub-nav a:not(:first-child) {
  margin-left: 1.4vw;
}

.sub-nav .active {
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 800;
  line-height: 1.8em;
  font-size: 1rem;
  color: #000;
  padding: .1em 0;
  opacity: 0.6;
  text-decoration: none;
}

.footer {
    padding-top: 80px;
    padding-bottom: 20px;
    padding-left: 10%;
    padding-right: 10%;
    overflow: hidden;
}

.footer-sub-nav {
  text-align: center;
}

.footer-sub-nav a {
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.8em;
  font-size: 1rem;
  color: #000;
  padding: .1em 0;
  opacity: 0.8;
  text-decoration: none;
}

.footer-sub-nav a:not(:first-child) {
  margin-left: 1.4vw;
}

.footer h2 {
  margin-top: 10px;
  margin-bottom: 0;
}

.footer h2 > a {
  text-decoration: none;
  color: #333;
  opacity: 60%;
  font-weight: 700;
  font-size: 20px;
}

/* css for active class
.active {
  font-weight: bolder;
}
*/

.intro-header {
  background-color: #f0f4ff;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 40px;
  padding-bottom: 40px;
  margin-top: 0;
  margin-bottom: 40px;
  text-align: left;
}

.intro-header h1 {
  font-size: 40px;
  line-height: 48px;
  margin: 0;
}

.intro-header p {
  font-size: 18px;
  line-height: 24px;
  margin-top: 10px;
  margin-bottom: 0px;
}

.section-header {
  text-align: left;
  margin-left: 10%;
  opacity: 60%;
}

.section-header h3 {
  opacity: 80%;
  font-size: 18px;
  weight: 800;
}

.projects-list {
  margin-left: 10%;
  margin-right: 10%;
  text-align: left;
}

.project-summary {
  display: flex;
  align-items: flex-start;
  margin-bottom: 40px;
}

.project-summary-reversed {
  display: flex;
  align-items: flex-start;
  margin-bottom: 40px;
}

.project h1 {
  text-align: left;
  margin-top: 40px;
  margin-bottom: 0;
  margin-left: 15%;
  font-size: 40px;
}

.project-details {
  width: 55%;
}

.project-summary-reversed .project-details {
  text-align: right;
}

.project-details a {
  text-decoration: none;
}

.project-details h2 {
  font-size: 30px;
  font-weight: 900;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #000;
}

.project-keywords {
  font-size: 16px;
  line-height: 20px;
  margin-top: 0;
  color: #999;
}

.project-details p {
  font-size: 18px;
  line-height: 1.75;
  color: #888;
}

.project-summary-reversed .project-image {
  width: 40%;
  margin-left: 30px;
  margin-right: 0;
}

.project-image {
  width: 40%;
  margin-right: 30px;
}

.project-image img {
  max-width: 100%;
  max-height: 250px;
  box-shadow: 0 10px 60px 0 rgb(0 0 0 / 20%);
  transition: all .65s;
  animation-delay: .25s;
}

.project-summary .project-image {
  text-align: right;
}

.project-image img:hover {
  transform: scale(1.05);
}

.project-section {
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  align-items: flex-start;
}

.coloredBG {
  background-color: #f0f4ff;
}

.project-section-title {
  width: 184px;
  margin-left: 15%;
  text-align: left;
}

.project-section-title h2 {
  font-family: 'PT Serif', serif;
  margin: 0;
  position: relative;
  width: inherit;
  font-size: 26px;
  color: #5c5c5c;
  line-height: 1.5;
  font-weight: 600;
  text-transform: none;
}

.project-section-description {
  margin-right: 15%;
  margin-left: 53px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  text-align: left;
}

.project-section-description p {
  margin-top: 0;
}

.project-section-description ul {
  margin-top: 0;
}

.project-section-description img {
  width: 100%;
}

.project-section-description figure {
  margin: 30px 0 0 0;
}

figcaption {
  font-style: italic;
  text-align: center;
  color: #777;
  font-size: 16px;
}

.project-section-images {
  margin-right: 15%;
  margin-left: 53px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  text-align: left;
  margin-bottom: -30px;
}

.project-section-images figure {
  margin: 0 0 30px 0;
  text-align: center;
}

.project-section-images .mobile-screenshot img {
  width: 100px;
}

.project-section-images img {
  width: 100%;
}

.supplementary {
  padding-top: 0;
}


.image-section {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 4px;
}

.image-column {
  flex: 28%;
  max-width: 28%;
  padding: 0 4px;
}

.image-section img {
  width: 100%;
  margin-top: 8px;
  vertical-align: middle;
}

.image-section figure {
  margin: 0;
}

.image-section figcaption {
  font-style: italic;
  text-align: center;
  line-height: 24px;
}

.resume-section {
  display: flex;
  align-items: flex-start;
  margin-top: 30px;
}

.resume-section-supplementary {
  display: flex;
  align-items: flex-start;
  margin-top: 0;
}

.resume-section-title {
  width: 186px;
  margin-left: 10%;
  font-family: "Helvetica-Neue", Helvetica, Arial, sans-serif;
  color: #5c5c5c;
  text-align: right;
}

.resume-section-title h4 {
  margin: 0;
  position: relative;
  width: inherit;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
  text-transform: none;
}

.resume-section-title .resume-company {
  font-size: 18px;
  color: #000;
  font-weight: bold;
}

.resume-section-description {
  margin-right: 10%;
  margin-left: 30px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  text-align: left;
}

.about-section {
  display: flex;
  align-items: flex-start;
  margin-top: 40px;
}

.about-photo {
  width: 300px;
  margin-left: 10%;
}

.about-photo img {
  width: inherit;
}

.about-content {
  margin-right: 10%;
  margin-left: 30px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 400;
  text-align: left;
}

.about h2 {
  text-align: left;
  margin-top: 30px;
  margin-bottom: 20px;
  font-size: 30px;
}

.about-content p {
  margin-top: 0;
}

.mtribes-page-header {
  background-image: url('./images/mtribes-hero.png');
  background-size: cover;
  height: 50vh;
}

.axis-page-header {
  background-image: url('./project-images/axis/axis-header.jpg');
  background-size: cover;
  background-position: center left;
  height: 50vh;
}

.classbento-page-header {
  background-image: url('./project-images/classbento/homepage.png');
  background-size: cover;
  background-position: 0% 25%;
  height: 50vh;
}

@media screen and (max-width: 600px) {
  .navigation {
    padding-top: 2vw;
    padding-bottom: 2vw;
    padding-left: 3vw;
    padding-right: 3vw;
  }

  .intro-header {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 24px;
    padding-bottom: 24px;
    margin-bottom: 20px;
  }

  .intro-header h1 {
    font-size: 26px;
    line-height: 1.2;
  }

  .intro-header p {
    font-size: 16px;
    line-height: 1.3;
  }

  .section-header {
    margin-left: 5%;
  }

  .projects-list {
    margin-left: 5%;
    margin-right: 5%;
  }

  .project-summary {
    margin-bottom: 30px;
  }

  .project-summary-reversed {
    margin-bottom: 20px;
  }

  .project-details h2 {
    font-size: 24px;
    margin-top: 0;
  }

  .project-keywords {
    margin-bottom: 0;
    font-size: 14px;
  }

  .project-details p {
    font-size: 16px;
    margin-top: 10px;
    line-height: 22px;
  }

  .project-section {
    padding-top: 20px;
    padding-bottom: 20px;
    display: block;
  }

  .project h1 {
    margin-left: 5%;
    font-size: 30px;
    margin-top: 20px;
  }

  .project-section-title h2 {
    font-size: 22px;
  }

  .project-section-title {
    width: auto;
    margin-left: 5%;
    margin-bottom: 20px;
  }

  .project-section-description {
    margin-left: 5%;
    margin-right: 5%;
    font-size: 16px;
  }

  .project-section-description figure {
    margin: 20px 0 0 0;
  }

  .image-column {
    flex: 100%;
    max-width: 100%;
  }

  .axis-page-header {
    background-position: center center;
  }

  .classbento-page-header {
    height: 40vh;
  }

  .resume-section {
    display: block;
  }

  .resume-section-supplementary {
    display: block;
  }

  .resume-section-title {
    margin-left: 5%;
    margin-right: 5%;
    width: auto;
    text-align: left;
    margin-bottom: 20px;
  }

  .resume-section-description {
    margin-left: 5%;
    margin-right: 5%;
    font-size: 16px;
  }

  .about-section {
    display: block;
    margin-top: 20px;
  }

  .about-photo {
    margin-left: 5%;
    margin-bottom: 20px;
    width: 200px;
  }

  .about-content {
    margin-left: 5%;
    margin-right: 5%;
    font-size: 16px;
  }

  .about h2 {
    font-size: 24px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
